<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <el-input class="buy-in"
                  type="number"
                  v-model="buyInAmount"
                  placeholder="輸入下單金額">
        </el-input>
        <el-input class="buy-in"
                  type="number"
                  v-model="buyInUnit"
                  placeholder="輸入下單單位">
        </el-input>
        <el-button @click="buySubmit">{{ btnName }}</el-button>
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    name: "BuyIn",
    data() {
        return {
            btnName: "送出",
            buyInAmount: "",
            buyInUnit: "",
            buyInObj: {
                title: "buy-in",
                buyInDate: new Date(),
                buyInAmount: 0,
                buyInUnit: 0,
            },
        };
    },
    props: {
        msg: String,
    },
    methods: {
        ...mapActions("home", ["a_createBuyInAmount"]),
        buySubmit() {
            const vm = this;
            vm.buyInObj.buyInAmount = Number(vm.buyInAmount);
            vm.buyInObj.buyInUnit = Number(vm.buyInUnit);
            vm.a_createBuyInAmount(vm.buyInObj);
        },
    },
};
</script>

